.App {
  text-align: center;
  margin-top: 50px;
}

.App .first_form{
  width: 90%;
  max-width: 500px;
  margin: 0 auto;
  padding: 30px;
  border: 1px dashed #ccc;
}
.App .first_form .form_line{
  margin-bottom: 50px;
}
.App .first_form .form_line label{
  font-size: 20px;
  display: block;
  margin-bottom:5px;
  text-align: left;
}
.App .first_form .form_line .input{
  width: 100%;
  height: 40px;
}
.App .first_form .form_line .input input{
  outline: none;
  border-radius: 4px;
  border: 1px solid #ccc;
  width: 100%;
  height: 100%;
}
.App .first_form .form_line .textarea{
  height: 200px;
  width: 100%;
}
.App .first_form .form_line .textarea textarea{
  height: 100%;
  width: 100%;
  border-radius: 4px;
  border: 1px solid #ccc;
}
.App .first_form .form_line .submit{
  text-align: center;
}
.App .first_form .form_line .submit button{
  display: inline-block;
  padding: 20px 40px;
  color: #fff;
  background: blue;
  border-radius: 20px;
  cursor: pointer;
  -webkit-box-shadow: none;
          box-shadow: none;
  font-size: 18px;
  border: none;
}


